@import url(https://fonts.googleapis.com/css2?family=Play&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Play";
}
a {
  text-decoration: inherit;
  color: inherit;
}

.app {
  min-height: 100vh;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.title {
  text-transform: uppercase;
  font-size: 8vw;
  font-family: "Play";
  cursor: pointer;
}

.divider {
  margin: 10px;
  width: 97%;
  border-color: #324658;
}

.content {
  display: flex;
  justify-content: space-around;
}

.settings {
  width: 45%;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Play";
  font-weight: 300;
}

.settings_select {
  display: flex;
  flex-direction: column;
  padding: 20px;
  width: 100%;
  flex: 0.8 1;
  justify-content: space-evenly;
}

.banner {
  width: 40%;
  align-self: center;
  padding: 8px;
  border-radius: 15px;
}

@media (max-width: 800px) {
  .content {
    flex-direction: column-reverse;
    align-items: center;
  }

  .banner {
    width: 85%;
    padding: 0;
    padding-top: 35px;
  }
  .settings {
    width: 100%;
  }
}

.error-message {
  width: 100%;
  padding: 10px;
  background-color: orangered;
  margin-bottom: 10px;
  border-radius: 5px;
  text-align: center;
  color: white;
  text-transform: uppercase;
}

.quiz {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.subtitle {
  font-size: 25px;
  padding: 5px 10px;
  border: 1px solid black;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.quiz_info {
  width: 100%;
  display: flex;
  justify-content: space-around;
  text-transform: uppercase;
  margin: 10px;
}

.question {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
}

.single_question {
  width: 95%;
  min-height: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  border: 5px solid grey;
  padding: 20px;
  border-radius: 20px;
  margin-top: 10px;
  text-align: center;
}

.options {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex: 1 1;
  align-items: center;
  justify-content: space-around;
  margin: 10px;
}

.single_option {
  width: 46%;
  height: 50px;
  padding: 15px 20px;
  margin: 10px;
  box-shadow: 0 0 2px black;
}

.controls {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

@media (max-width: 614px) {
  .single_question {
    width: 100%;
  }
}

.select {
  background-color: rgb(7, 207, 0);
  color: rgb(255, 255, 255);
  box-shadow: 0 0 1px black;
}

.wrong {
  background-color: rgb(233, 0, 0);
  color: white;
  box-shadow: 0 0 1px black;
}

.result {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 60vh;
  text-align: center;
}

